type TreeNodeType = {
  val: string
  childrens: TreeNodeType[]
  parent?: TreeNodeType | null
}

class TreeNode implements TreeNodeType {
  val: string
  childrens: TreeNodeType[]
  parent?: TreeNodeType | null

  constructor(val: string) {
    this.val = val
    this.childrens = []
    this.parent = null
  }

  addChild(child: TreeNodeType) {
    child.parent = this
    this.childrens.push(child)
    return this
  }

  removeChild(child: TreeNodeType) {
    const index = this.childrens.indexOf(child)
    if (index > -1) {
      this.childrens.splice(index, 1)
    }
    child.parent = null
    return this
  }
}

type TreeType = {
  root: TreeNodeType
}

class Tree implements TreeType {
  root: TreeNodeType
  constructor(val: string) {
    this.root = new TreeNode(val)
  }

  getRoot() {
    return this.root
  }

  getNode(val: string, node: TreeNodeType = this.root) {
    if (node.val === val) {
      return node
    }
    for (let i = 0; i < node.childrens.length; i++) {
      const child = node.childrens[i]
      const result = this.getNode(val, child)
      if (result) {
        return result
      }
    }
    return null
  }
  addNode(val: string, parent: string) {
    const parentNode = this.getNode(parent)
    if (parentNode) {
      const node = new TreeNode(val)
      parentNode.addChild(node)
    }
  }
}
